@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,700;1,200&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/* width */
::-webkit-scrollbar {
    width: 8px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 20px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(0, 204, 255); 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #026983; 
  }

*{
    margin:0;
    padding: 0;
}
body{
    font-family: 'Poppins', sans-serif;
}
main{
    min-height: 100vh;
    background: linear-gradient(to right top, #b5f0e5, #4bdbf1);
    position: relative;
    display: flex;
    justify-content: center;
    align-items:center ;
    overflow-y: hidden;
}

.glass {
    background-color: #fff;
    background:linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3) ); 
    position: absolute;
    height: 85vh;
    width: 75%;
    border-radius: 1.5rem;
    z-index: 10;
    -webkit-backdrop-filter: blur(1rem);
            backdrop-filter: blur(1rem);
}

.circle1, .circle2{
    height: 10rem;
    width: 10rem;
    border-radius: 5rem;
    position: absolute;
    background-color: #fff;
    background:linear-gradient(to right bottom, rgba(255, 255, 255, .8), rgba(255, 255, 255, 0.3) ); 
}

.circle1 {
    bottom: 3%;
    left:8%;
}

.circle2{
    top:3%;
    right: 8%;
}

.closed{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    color:#666;
}

.closed .locked{
    background-color: #f1f1f1;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 40vh;
    border:1px solid #fff;
} 
.locked img {
    height: 100px;
    width: 100px;
    background-color: #fff;
    padding: 1em;
    border-radius: 50%;
    margin: 2rem auto;

}

.locked small {
    font-weight: 700;
    color: #222;
} 

.closed h2 {
    font-weight: 700;
    font-size: 2rem;
    color: rgba(58, 122, 218, 0.829);
}

.closed a {
    text-decoration: none;
    margin-top: 1em;
    font-weight: 400;
}


.open{
    display: flex;
    height: 100%;
}

.open .menu {
    background-color: rgba(255, 255, 255, 0.397);
    height: 100%;
    width: 30%;
    border-top-left-radius: 1.5rem;
    border-bottom-left-radius: 1.5rem;
}
.menu .companyLogo{
    height: 25%;
    padding: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-transform: capitalize;
    color: #555;
    background-color: #f1f1f1;
    border-top-left-radius: 1.5rem;

}
.companyLogo img{
    height: 60px;
    width: 60px;
    background-color: #fff;
    padding: .4em;
    border-radius: 50%;
    margin: .6em auto;
}

.menu .links{
    padding: 1em;
    height: 63%;
    margin-left: 20px;
}


.links p{
  font-size: 1rem;
  font-weight: 400;
  color: #444;
  padding: .8em 0;
  transition: all .3s;
}

.links p:hover {
    font-weight: 700; 
}


.links p a i {
  margin-right: 20px;
}

.selectedProjectMenu{
   margin-top: 2em;
   border-top: .2px solid rgb(120, 158, 241);
   padding-top: 1em;
}

.selectedProjectMenu p {
    margin-bottom: 1em;
}

.completed, .running, .pending {
  padding: 10px;
  background: rgba(255, 255, 255, 0.377);
  margin: .6em auto;
}

.completed {  border-left: 4px solid lime; } 
.running{   border-left: 4px solid orange;} 
.pending{   border-left: 4px solid red;}

.menu .version{
    height: 10%;
    padding:0px 1em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
}

.version a {
    font-size: 12px;
    text-decoration: none;
    color:#555;
    text-align: center;
}

.version small {
    font-size: 12px;
    color:#222;
    font-weight: 600;
    text-align: center;
}



/* content area stuff */

.content {
    color:#444;
    width: 100%;
    border-top-right-radius: 1.5rem;
    overflow-y: scroll;


}

.projects{
    width: 100%;
}


.greetings{
    height: 22vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    border-top-right-radius: 1.5rem;
    background-color: rgba(5, 156, 202, 0.493);
    color:rgb(29, 28, 28);
}



.greetings h2 {
    font-size: 2rem;
}


.list{
    padding: 1.6rem;
}
.list-title{
  text-transform: uppercase;
  font-size: 12px;
  margin-bottom: 10px;
}

.project__card{
    width: 48%;
    border-radius: 1rem;
    background-color: #fff;
    margin-bottom: 2em;
}

.project__header {
    background-color: #555;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    padding: 1em;
    color:#fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.project__header h2 {
    font-size: 1em;
    text-transform: uppercase;
    margin-bottom: 2px;
    font-weight: 700;
}

.project__header p {
    font-size: .8em;
}

.project__details{
    padding: 1em;
    font-size: 15px;
}

.project__action{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid #eee;
    padding: 1em;
}

.project__action  div{
    flex:1 1;
    text-align: center;
}

.project__action  div +  .project__action  div{
   border-right: 1px solid #333;
}

.project__action a {
    text-decoration: none;
    color:#555;
    font-weight: 500;
    font-size: .8rem;
    cursor: pointer;
}

.project__action i{
    margin-right: 10px;
}

.projectRow {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}



.project__detailsHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: palevioletred;
    min-height: 21vh;
}
.project__detail{
    flex: 2 1;
    border-right: 1px solid #eee;
    padding: 1em;
    height: 100%;
}

.project__detail h1{
    font-size: 2.3rem;
    font-weight: 700;
}
.project__stats{
    flex:1 1;
    padding: 1em;
    height: 100%;
}

.project_statBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.project_stat{
   text-align: center;
   padding: 1em;
}

.project_stat small{
    text-transform:uppercase;
    font-size: 12px;

}

.project_stat span{
    text-transform:lowercase;
    font-size: 12px;
}

.project_stat p {
    font-weight: bold;
    font-size: 1.2rem;
    margin-top: 10px;
}

.project__taskList{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.project__taskList h4{
text-transform: uppercase;
font-size: .8rem;
font-weight: bold;
margin-bottom: 16px;

}

.project__task {
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: .8em;
    align-items: center;
    margin: 18px auto;
}
.task_form{
    flex:1 1;
    padding: 1em;
}

.task_form form {
    background-color: #fff;
    border-radius: 1.2rem;
    min-height: 50vh;
    padding: 1em;
}

.tasks {
    flex:2 1;
    padding: 1em;
}

.project__taskInfo{
    flex-grow: 1;
}

.project__taskActions{
   width: 30%;
   display: flex;
   align-items: flex-end;
   justify-content: flex-end;
}



.field{
    display: flex;
    flex-direction: column;
    margin-top:1em;
   
}
label{
    color:#444;
    font-size: .9rem;
    margin-bottom: .6em;
}
input {
    height: 3em;
    padding:.4em 1em;
    border: 1px solid #aaa;
    width: calc(100%-2em);
    font-family: 'Poppins', sans-serif;
}

input[type=text]:focus {
    border:none;
    background-color: #f1f1f1;
}


select{
    height: 3em;
    padding:.3em 1em;
    width: calc(100%-2em);
    font-family: 'Poppins', sans-serif;
    background-color: #fff;
    border: 1px solid #aaa;
}
.exp{
    font-size: .6rem;
    color:gray;
    text-align: right;
    margin-top: .4em;
}
textarea{
    padding:.4em 1em;
    border: 1px solid #aaa;
    width: calc(100%-2em);
    font-family: 'Poppins', sans-serif;
}

button {
    background-color: #026875;
    border: none;
    color: white;
    padding: 14px 40px;
    text-decoration: none;
    margin: 10px 2px;
    cursor: pointer;
    width: 100%;
    transition: all .4s;
    border-radius: 5px;
}

button:hover{
    background-color: #008394;
}

.projectForm{
    position: absolute;
    z-index: 10;
    background-color: rgba(29, 2, 2, 0.7);
    padding: 1em;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}


.projectForm form {
     background-color: #fff;
     padding: 1em;
     border-radius: 1em;
}

.projectForm a {
    cursor: pointer;
}

.colorBox {
    display: flex;
    flex-direction: row;
}
.colorBox input {
    flex:1 1
}

.colorBox div {
    flex:2 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;
}

.colorPIcker {

}

.topBarBtn {
    background-color: rgba(5, 156, 202, 0.596);
    height: 100%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:1em;
    width: 20%;
    color: #fff;

}

.greetings div{
    padding: 1em;
    background-color: rgba(93, 236, 213, 0.192);
    height: 100%;
    width: 80%;
 }
